





























import { Component, Vue } from 'vue-property-decorator';

@Component
export default class App extends Vue {
  selectedLang = 'En';

  languages = [
    { type: 'en', flag: 'US' },
    { type: 'es', flag: 'ES' }
  ]

  showSelect = false;

  showList = false;

  selectLang(lang: string) {
    this.selectedLang = lang;
    this.showList = false;
    this.$i18n.locale = lang;
  }
}
